@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}.app{width:100%;min-height:100vh;background-image:linear-gradient(257deg, #59145c, #262d92);font-family:"Roboto",sans-serif}.form{display:flex;flex-direction:column;align-items:flex-start;margin:0 auto;padding:35px;width:100%;max-width:700px;min-width:320px;min-height:500px;background-color:rgba(145,127,137,.62);border-radius:3px;box-shadow:0 9px 23px 0 rgba(0,0,0,.03)}.form h2{text-align:center;font-size:28px;text-transform:uppercase}.form label{display:block;margin-top:25px;font-size:18px;font-weight:bold}.form input{width:100%;height:50px;margin-top:15px;padding:5px 15px;background:#fff;border:1px solid #e2e2e2;color:#353535;border-radius:10px;font-size:22px}.form input:focus{outline:1px solid #59145c}.form select{height:50px;margin-top:15px;padding:0 20px;font-size:18px;text-align:center;-webkit-appearance:none;-moz-appearance:none;appearance:none;background:#fff;border:1px solid #e2e2e2;color:#353535;border-radius:10px;cursor:pointer}.form select:focus{outline:1px solid #59145c}.form textarea{height:180px;width:100%;margin-top:15px;padding:15px 20px;font-size:18px;background:#fff;border:1px solid #e2e2e2;color:#353535;border-radius:10px;resize:none}.form textarea:focus{outline:1px solid #59145c}.form input[type=checkbox]{display:inline;font-size:16px;width:25px;height:25px;margin-top:0;margin-right:15px}.form .checkbox{display:flex;align-items:center}@media(max-width: 430px){.form .checkbox{font-size:12px}}.form button{display:block;width:180px;padding:15px;margin-top:25px;font-size:18px;color:#fff;border:0;border-radius:10px;background-image:linear-gradient(257deg, #59145c, #262d92);cursor:pointer}.form button:active{transform:scale(0.9)}.form .error{color:#e53e3e;margin-top:8px}
